  async retweetTweet({ _ }, {tweetId, userId, username, text}) {
    const ownerNodeId = sessionStorage.getItem("ownerNodeId");
    const ownerUserId = sessionStorage.getItem("ownerUserId");
    let id = await client.sendMessage({
      path: PUBLIC_POST_RETWEET,
      node_id: ownerNodeId,
      timestamp: new Date().toISOString(),
      body: {
        id: tweetId,
        user_id: userId,
        username: username,
        text: text,
        retweeted_by: ownerUserId,
        created_at: new Date().toISOString(),
      },
    })

    return await client.getResponse(id) // retweets count
  },
