import router from "../../../router";
import QRCode from "qrcode";
import { getClient } from "@/lib/client";

const PRIVATE_POST_LOGIN = "/private/post/login/0.0.0"
const PRIVATE_POST_LOGOUT = "/private/post/logout/0.0.0"
const PUBLIC_GET_USER = "/public/get/user/0.0.0"


const client = getClient();

export function connectClient() {
  if (client.socket && client.socket.readyState === WebSocket.OPEN) {
    console.log("WebSocket is already connected.");
    return;
  }
  client.connect()
}

export default {
  client,

  loginUser({ commit }, user) {
    commit("USER_LOGIN", user);
  },
  async logoutUser({ commit, dispatch }) {
    const owner = JSON.parse(sessionStorage.getItem("ownerProfile"));
    await client.sendMessage({
      path: PRIVATE_POST_LOGOUT,
      node_id: owner.node_id,
    })
    client.close();
    sessionStorage.clear();

    commit("USER_LOGOUT");
    dispatch("signup/setSignupStep", "", { root: true });
    dispatch("warpnet/unsubscribeNotifications", null, { root: true });
    dispatch("warpnet/resetState", null, { root: true });
    dispatch("profilePage/resetState", null, { root: true });
    await router.push("/");
  },

  async signInUser({ dispatch, commit }, form) {
    let id = await client.sendMessage({
          path: PRIVATE_POST_LOGIN,
          node_id: "None",
          body: {
            username: form.username,
            password: form.password,
          }
        });

    let resp = await client.getResponse(id)
    if (!resp) {
      alert("authentication failed")
      return;
    }

    const ownerStr = JSON.stringify(resp.owner)
    const qrData = ownerStr;

    resp.token = null // for security reasons
    sessionStorage.setItem("ownerUserId", resp.owner.user_id);
    sessionStorage.setItem("ownerNodeId", resp.owner.node_id);

    let idd = await client.sendMessage({
      path: PUBLIC_GET_USER,
      node_id: resp.owner.node_id,
      timestamp: new Date().toISOString(),
      body: {
        user_id: resp.owner.user_id,
      },
    })
    const profile = await client.getResponse(idd)
    const profileStr = JSON.stringify(profile)
    sessionStorage.setItem("ownerProfile", profileStr);

    const qrOptions = {
      color: {
        dark: "#c5007f",
        light: "#ffffff",
      },
    };

    try {
      const qrCodeURL = await QRCode.toDataURL(qrData, qrOptions);
      console.log("QR Code Generated:", qrCodeURL);

      commit("SET_QR_CODE", qrCodeURL); // Добавьте в state QR-кода

    } catch (err) {
      console.error("Error generating QR code:", err);
    }
    commit("USER_SIGNUP", profile);
    await dispatch("loginUser", profile);
    await router.push({ name: "Home" });
  },

  async openQRCodeModal({ commit }) {
    commit("SET_QR_MODAL_OPEN", true);
  },

  async closeQRCodeModal({ commit }) {
    commit("SET_QR_MODAL_OPEN", false);
  },
};
